import React from 'react';
import Category from './category';
import Register from './register';

class App1 extends React.Component {
constructor(props)
{
  super(props);
  this.state = {
  isClick:false
}
}

clickBooking=()=>{
this.setState({ isClick: true });
}

render()
{
  let res;
  const isClick1=this.state.isClick;

  res = <div className="wrapper">
    <div className="">
      <div className="row">

        <div className="col-12 text-center" style={{display:'block'}}>
          <img src="dist/img/DonkeyUser.png" style={{
    height: '128px',
    top: '15%',
    width: '128px',
    marginTop:'15%'
  }} alt="" />
          <br />
          <br />
          <h3>Thanks for Booking..!!</h3>
          <h4>Your Order will be taken soon..!!</h4>
        </div>

      </div>
    </div>
  </div>;

  if(isClick1)
  return <Register />
  else
  return (res);
}
}

export default App1;
